.contacts {
  width: 75%;
  min-height: 60vh;
  display: grid;
  grid-template-areas: 'main-text main-text phone phone'
    'support order order order'
    'support order order order'
    'support address address address';
  grid-template-columns: 40% auto 20% 10%;
  gap: 20px;
  box-sizing: border-box;
  z-index: 5;
}

.contacts__paragraph {
  padding: 30px;
  box-sizing: border-box;
  margin: 0;
  border-radius: 30px;
  height: 100%;
  text-align: justify;
  font-size: 18px;
  background: rgba(241, 240, 240, 0.8);
  color: #3d3d3d;
  display: flex;
  flex-direction: column;
  font-weight: 600;
}

.contacts__link {
  font-size: 16px;
  text-align: right;
  height: 100%;
  max-width: 300px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  justify-self: flex-end;
  align-self: flex-end;
  color: #099ad7;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  transition: color .3s ease-in-out;
}

.contacts__link:hover {
  color: #3d3d3d;
}

.contacts__main-text {
  grid-area: main-text;
  background: rgb(9, 153, 215);
  color: rgb(241, 240, 240);
  font-weight: 200;
}

.contacts__address {
  grid-area: address;
  background-image: url(../../images/about-card-blue-triangle.png);
  background-repeat: no-repeat;
  background-position: -160px -60px;
  background-size: auto 230px;
  color: #fff;
}

.contacts__support {
  grid-area: support;
  animation: gradient 3s linear infinite;
  position: relative;
  background-image: url(../../images/animation-dark-blue-triangle.png);
  background-repeat: no-repeat;
  background-position: -150px -150px;
  background-size: auto 350px;
  color: #fff;
}

.contacts__order {
  grid-area: order;
  background-image: url(../../images/animation-green-triangle.png);
  background-repeat: no-repeat;
  background-position: 230px -280px;
  background-size: auto 350px;
  min-height: 158px;
}

.contacts__phone {
  grid-area: phone;
  background-image: url(../../images/about-card-green-triangle.png);
  background-repeat: no-repeat;
  background-position: -240px 105px;
  background-size: auto 230px;
}

@media (max-width:1000px) {
  .contacts__paragraph {
    font-size: 14px;
  }

  .contacts__link {
    font-size: 14px;
    max-width: 250px;
  }
}

@media (max-width:850px) {
  .contacts {
    width: 90%;
    display: flex;
    flex-direction: column;
    width: 90%;
  }

  .contacts__link {
    font-size: 14px;
    max-width: 90%;
  }

  .contacts__paragraph {
    width: 100%;
  }

  .contacts__order {
    min-height: 0px;
    background-image: none;
  }

  .contacts__address {
    background-image: none;
    color: #3d3d3d;
  }

  .contacts__support {
    background-image: none;
    color: #3d3d3d;
  }

  .contacts__phone {
    background-image: none;
  }
}