.event-page {
  display: grid;
  grid-template-areas: 'back about'
    'title about'
    'list about';
  grid-template-columns: auto 60%;
  grid-template-rows: 80px 220px 400px;
  gap: 20px;
}

.event-page__back-btn {
  grid-area: back;
  background: rgba(15, 160, 221, .8);
  border: none;
  padding: 30px;
  box-sizing: border-box;
  margin: 0;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all .3s linear;
}

.event-page__back-btn:hover {
  background: rgba(9, 154, 215, 1);
  cursor: pointer;
  transform: scaleX(1.01) translateX(-5px);
}

.event-page__back-btn:hover .event-page__back-btn-text {
  color: #f1f0f0;
  font-weight: normal;
}

.event-page__back-btn-text {
  margin: 0;
  text-align: justify;
  font-size: 18px;
  color: #3d3d3d;
  font-weight: bold;
  text-transform: uppercase;
}

.event-page__title-tile {
  grid-area: title;
  align-items: flex-end;
  justify-content: space-between;
  text-transform: uppercase;
  color: #099ad7;
  height: 100%;
  background: #f1f0f0;
}

.event-page__title {
  margin: 0;
  padding: 0;
  color: #099ad7;
  font-size: 28px;
}

.event-page__logo {
  width: 60%;
  max-width: 250px;
  align-self: flex-start;
}

.event-page__list {
  grid-area: list;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.event-page__about {
  grid-area: about;
  height: 100%;
  box-sizing: border-box;
  width: 100%;
}

.event-page__scores {
  align-self: center;
}

.event-page__table {
  max-width: 500px;
  width: 100%;
  align-self: center;
  margin-top: 15px;
}

@media (max-width: 1250px) {

  .event-page__title {
    font-size: 24px;
  }

}

@media (max-width: 1200px) {

  .event-page {
    display: grid;
    grid-template-areas: 'back list'
      'title list'
      'about about';
    grid-template-rows: 80px 300px 500px;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

}

@media (max-width: 750px) {

  .event-page {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }


  .event-page__list li {
    margin-top: 15px;
  }


  .event-page__list li:first-of-type {
    margin: 0;
  }

  .event-page__about {
    max-height: 500px;
  }

  .event-page__title {
    font-size: 18px;
  }

  .event-page__back-btn {
    padding: 10px;
    border-radius: 30px;
  }

}