.tile-with-scroll {
  padding: 30px 0 30px 30px;
}

.tile-with-scroll__container {
  padding: 0 20px 0 0;
  box-sizing: border-box;
  margin: 0;
  border-radius: 10px;
  text-align: justify;
  font-size: 14px;
  color: #3d3d3d;
  overflow-y: scroll;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
}

.tile-with-scroll__container {
  scrollbar-color: #099AD7 #E9E9E9;
  scrollbar-width: thin;
}

.tile-with-scroll__container::-webkit-scrollbar {
  width: 10px;
}

.tile-with-scroll__container::-webkit-scrollbar-track {
  background: #E9E9E9;
  border-radius: 15px;
}

.tile-with-scroll__container::-webkit-scrollbar-thumb {
  background-color: #099AD7;
  border-radius: 15px;
  padding-right: 30px;
  border: 3px solid #E9E9E9;
}


