@keyframes rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(-360deg);
  }
}

html {
  scroll-behavior: smooth
}

.rotation {
  animation: rotation linear infinite 60s;
}

.background {
  background-color: #d5d4d4;
}

.page {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Myriad Pro', sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  word-wrap: break-word;
  width: 100%;
  margin: 0 auto;
  scroll-behavior: smooth;
}

.section {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-items: stretch;
  align-items: flex-start;
  max-width: 1280px;
  width: 75%;
  margin-top: 130px;
  box-sizing: border-box;
  position: relative;
  z-index: 20;
}

.hidden {
  display: none;
}

.text {
  text-align: justify;
  font-size: 20px;
  font-weight: normal;
  line-height: 1.5;
  z-index: 5;
  margin: 0;
  padding: 0;
}

.text_uppercase {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
}

.link {
  color: #3d3d3d;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  transition: color .3s linear;
}

.link:hover {
  color: #099ad7;
}

/* Выделение текста цветом модуля */

.highlighted-text {
  color: #099ad7;
}

.highlighted-text_level_aero {
  color: #BE312A;
}

.highlighted-text_level_basic {
  color: #509ADB;
}

.highlighted-text_level_elementary {
  color: #63BD4C;
}

.highlighted-text_level_preliminary {
  color: #D8B13C;
}

.highlighted-text_level_professional {
  color: #3776C5;
}

.highlighted-text_level_research {
  color: #A85B9F;
}

.highlighted-text_level_machines {
  color: #C63332;
}

@media (max-width: 1200px) {
  .section {
    margin-top: 160px;
  }
}

@media (max-width: 1050px) {
  .section {
    width: 90%;
    margin-top: 140px;
  }
}

@media (max-width: 850px) {

  .text {
    font-size: 17px;
  }


  .link {
    font-size: 16px;
  }

}