.event {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 30px 0 0 0;
}

.event__item {
  transition: background-color .3s linear;
  font-size: 18px;
  font-style: normal;
  text-transform: uppercase;
  font-weight: 600;
  align-items: flex-end;
  justify-content: space-between;
}

.event__promo {
  margin-right: 40px;
  min-width: 450px;
  transition: transform .3s linear;
}

.event__logo {
  max-width: 75%;
  max-height: 150px;
  align-self: flex-start;
}

.event__title {
  color: #099ad7;
  font-size: 19px;
  font-style: normal;
  text-transform: uppercase;
  font-weight: 600;
  margin: 20px 0;
  text-align: justify;
}

.event__date {
  margin: 0;
}

.event__about {
  margin: 0;
  text-transform: none;
  font-size: 20px;
  font-weight: normal;
  transition: background-color .3s linear;
}

.event_link {
  text-decoration: none;
  width: 100%;
  margin: 0;
}

.event_link:hover .event__link-text {
  transform: scale(1.05);
  color: #fff;
}

.event_link:hover .event__item {
  background: rgba(15, 160, 221, .6);
}

.event_link:hover .event__promo {
  background-color: rgba(241, 240, 240, .9);
  transform: scale(1.02);
}

.event__link-text {
  justify-self: flex-end;
  color: #099ad7;
  padding: 10px;
  margin: 0;
  font-size: 20px;
  transition: transform .3s linear, color .3s linear;
}

@media (max-width: 1100px) {
  .event__promo {
    margin-right: 40px;
    min-width: 320px;
  }

  .event__logo {
    max-height: 150px;
    align-self: flex-start;
  }

  .event__title {
    text-align: right;
  }

  .event__item {
    font-size: 16px;
  }
}

@media (max-width: 900px) {
  .event__promo {
    margin: 0;
    min-width: 0;
    width: 100%;
  }

  .event__logo {
    width: 250px;
  }

  .event__item {
    font-size: 18px;
  }

  .event__about-item {
    display: none;
  }
}

@media (max-width: 550px) {

  .event__logo {
    width: 150px;
  }

  .event__title {
    font-size: 14px;
  }

  .event__item {
    font-size: 12px;
  }

}