.about {
  display: grid;
  grid-template-areas: 'main-text main-text main-text'
    'card-left card-center card-right'
    'provider provider provider';
  grid-template-rows: 200px auto;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  box-sizing: border-box;
  width: 75%;
  min-height: 60vh;
}

.about__paragraph {
  padding: 30px;
  box-sizing: border-box;
  margin: 0;
  border-radius: 30px;
  height: 100%;
  text-align: justify;
  font-size: 14px;
  background: rgba(241, 240, 240, 0.8);
  color: rgb(241, 240, 240)
}

.about__main-paragraph {
  grid-area: main-text;
  position: relative;
  z-index: 5;
  border-radius: 30px;
  background: rgba(240, 239, 239, 0.8);
}

.about__main-text {
  height: 200px;
  width: 60%;
  padding-left: 30px;
  font-size: 20px;
  position: relative;
  z-index: 5;
  background: none;
  color: #3d3d3d;
}

.about__provider {
  grid-area: provider;
  position: relative;
  z-index: 5;
  border-radius: 30px;
  background: rgba(240, 239, 239, 0.8);
  color: #3d3d3d;
}

.about__card-left {
  background: rgba(0, 118, 189, .9);
  grid-area: card-left;
  background-image: url(../../images/about-card-back.png);
  background-repeat: no-repeat;
  background-position: -80px -100px;
  background-size: auto 450px;
}

.about__card-center {
  grid-area: card-center;
  background: rgba(0, 118, 189, .9);
  background-image: url(../../images/about-card-blue-triangle.png);
  background-repeat: no-repeat;
  background-position: 150px 180px;
  background-size: auto 230px;
}

.about__card-right {
  grid-area: card-right;
  background-image: url(../../images/about-card-green-triangle.png);
  background-repeat: no-repeat;
  background-position: -80px 180px;
  background-size: auto 230px;
  color: #3d3d3d;

}

.about__kids {
  width: 500px;
  height: 500px;
  background-repeat: no-repeat;
  background-size: contain;
  position: fixed;
  z-index: 2;
}

@media (max-width: 1400px) {
  .about__main-text {
    font-size: 18px;
  }
}

@media (max-width: 1350px) {
  .about__main-text {
    width: 45%;
  }
}

@media (max-width: 1250px) {
  .about__main-text {
    font-size: 16px;
    line-height: 1.1;
  }
}

@media (max-width: 1100px) {
  .about {
    grid-template-areas: 'main-text'
      'card-left'
      'card-center'
      'card-right';
    grid-template-rows: 200px repeat(3, 150px);
    grid-template-columns: 1fr;
  }
}

@media (max-width: 950px) {
  .about {
    width: 90%;
  }
}

@media (max-width: 750px) {
  .about__main-text {
    width: 100%;
    font-size: 18px;
  }

  .about {
    width: 90%;
  }
}


@media (max-width: 550px) {

  .about {
    grid-template-rows: repeat(4, auto);
  }

  .about__main-text {
    height: 150px;
    font-size: 16px;
  }
}